.signup__wrap {
    background: $orange;
    padding: 80px 0;
}

.signup {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.signup__txt {
    font-size: px-to-rem(32px);
    line-height: px-to-rem(45px);
    font-weight: 300;
    h2 {
        font-size: px-to-rem(32px);
        line-height: px-to-rem(45px);
        font-weight: 500;
    }
}

.btn-black {
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    background: $black;
    border-radius: 5px;
    color: $white;
    padding: 16px 35px;
    font-size: px-to-rem(18px);
    margin-right: 16px;
}

.btn-transparent-black {
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    border: 1px solid $black;
    border-radius: 5px;
    color: $black;
    padding: 16px 35px;
    font-size: px-to-rem(18px);
}

@media (max-width: 900px) {
    .signup {
        flex-direction: column;
        justify-content: center;
    }
    .signup__txt {
        margin-bottom: 35px;
    }
}

@media (max-width: 500px) {
    .signup__txt {
        text-align: center;
    }
    .signup-btn {
        width: 100%;
    }
    .btn-transparent-black, .btn-black {
        display: block;
        max-width: 270px;
        margin: 0 auto 16px auto;
        text-align: center;
    }
}