.about {
    max-width: 1440px;
    margin: 0 auto;
    display: grid;
    grid-template-columns:  50% 50%;
    grid-template-areas: 'find imgright'
                        'imgleft work';
    font-size: px-to-rem(20px);
    line-height: px-to-rem(28px);
}

.about__find {
    display: grid;
    grid-area: find;
    justify-content: center;
    align-items: center;
    background: #f1f1f2;
    div{
        max-width: 440px;
    }
    h1 {
        font-size: px-to-rem(58px);
        line-height: px-to-rem(65px);
        font-weight: 500;
        margin-bottom: 45px;
    }
    p {
        margin-bottom: 36px;
    }
}

.about__img-right {
    grid-area: imgright;
}

.about__img-left {
    grid-area: imgleft;
}

.about__work {
    display: grid;
    grid-area: work;
    justify-content: center;
    align-items: center;
    background: #f1f1f2;
    div{
        max-width: 440px;
    }
    h2 {
        font-size: px-to-rem(32px);
        line-height: px-to-rem(45px);
        font-weight: 500;
        margin-bottom: 23px;
    }
    p {
        margin-bottom: 35px;
    }
}

.btn-orange {
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    background: $orange;
    border-radius: 5px;
    color: $white;
    padding: 20px 35px;
    font-size: px-to-rem(18px);
}

.btn-transparent-orange {
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    border: 1px solid $orange;
    border-radius: 5px;
    color: $orange;
    padding: 20px 45px;
    font-size: px-to-rem(18px);
}

@media (max-width: 1000px) {
    .about {
        grid-template-columns: 100%;
        grid-template-areas: 'imgright'
                             'find'
                             'imgleft'
                             'work';
    }
    .about__img-left, .about__img-right {
        background: #f1f1f2;
    }
    .about__img-left img, .about__img-right img {
        margin: 0 auto;
    }
    .about__work, .about__find {
        padding: 32px 20px;
    }
}

@media (max-width: 500px) {
    .about__find h1 {
        font-size: px-to-rem(40px);
        line-height: px-to-rem(50px);
        margin-bottom: 24px;
    }
}